import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useStudentAdmissionFeeCardScholarshipRevertToApplicable } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { useTranslation } from "react-i18next";

const StudentAdmissionFeesCardInstallmentCardGovToApplicableMenu = ({
  onClose,
  rid,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [studentAdmissionFeeCardScholarshipRevertToApplicable] =
    useStudentAdmissionFeeCardScholarshipRevertToApplicable();
  const onRemove = () => {
    if (rid) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardScholarshipRevertToApplicable({
        rid: rid,
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onRemove}>{t("transfer_to_applicable_fees")}</h6>
      </ModalMenu>
    </>
  );
};

export default StudentAdmissionFeesCardInstallmentCardGovToApplicableMenu;
