import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import PaymentGatewayConfig from "../../../../PaymentGateway/PaymentGatewayConfig";
import { get_initiate_paytmV2 } from "../../../../Decrypt/paytmUpiInitiative";
import { useParams } from "react-router-dom";
const StudentAdmissionPayFees = ({ onClose, install, admission }) => {
  const { t } = useTranslation();
  const params = useParams();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    amount: 0,
    type: "Admission",
    isTransaction: false,
  });
  const [errorField, setErrorField] = useState("");

  useEffect(() => {
    if (install?.remainAmount) {
      setState({
        amount: install?.remainAmount ?? 0,
        type: "Admission",
        isTransaction: false,
      });
    }
  }, []);
  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
    }));
  };

  const onCloseGateway = () => {
    setState((prev) => ({
      ...prev,
      isTransaction: false,
    }));
  };
  const onCollectFees = () => {
    let validation = requiredField({
      amount: state.amount,
    });
    if (existingRequiredField(validation)) {
      setState((prev) => ({
        ...prev,
        isTransaction: true,
      }));
    } else {
      setErrorField(validation);
    }
  };
  // console.info("applicant", install);
  const checkoutHandler = () => {
    let gatewayCharges = (+state?.amount * 2.1) / 100;
    let gst = (+gatewayCharges * 18) / 100;
    let withGst = gatewayCharges + gst;
    const data = parseInt(state?.amount);
    var paymentData = {
      amount: Math.ceil(data + +withGst.toFixed(2)),
    };
    get_initiate_paytmV2({
      amount: +state?.amount ?? 10,
      amount_nocharges: +paymentData.amount ?? 0,
      type: "Admission",
      paidBy: `${admission?.student?._id}`,
      paidTo: `${install?.instituteId}`,
      moduleId: `${install?.appId}`,
      ad_status_id: null,
      payment_installment: install?.installmentValue,
      ad_install: null,
      month: null,
      payment_remain_1: null,
      payment_card_type: null,
      payment_book_id: null,
      name: params.username,
      isApk: "web",
      payment_card_id: admission?._id ?? null,
    })
      .then((data) => {
        window.onScriptLoad(
          data?.response?.body?.txnToken,
          data?.order,
          data?.amount
        );
      })
      .catch((e) => {
        console.info(e);
      });
  };

  return (
    <>
      {state.isTransaction ? (
        <PaymentGatewayConfig
          onClose={onCloseGateway}
          allContent={state}
          checkoutAdmissionHandler={checkoutHandler}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("pay_admission_fees")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div className={style.admission_fees_row}>
                <h6>{t("applicable_pendig_fees_only")} :</h6>
                <h6>
                  {t("rs_only")}{" "}
                  {admission?.applicable_card?.applicable_fee ?? 0}
                </h6>
              </div>
              {/* <div className={style.admission_fees_row}>
            <h6>{t("remaining_fees")} :</h6>
            <h6>
              {t("rs_only")} {install?.remainAmount ?? 0}
            </h6>
          </div> */}
              <div className={style.admission_fees_row}>
                <h6>
                  {t("fees_category")} :{" "}
                  {admission?.fee_structure?.category_master?.category_name ??
                    ""}
                </h6>
              </div>
              <FormInput
                label={t("enter_amount_to_be_paid")}
                placeholder={t("please_enter_amount_paid")}
                name={"amount"}
                value={state.amount}
                type={"tel"}
                onChange={onInputAmount}
                errorShow={errorField["amount"]}
                // disabled={
                //   studentDesignationData?.student
                //     ?.online_amount_edit_access === "Granted"
                //     ? false
                //     : true
                // }
              />
              <CreateButton
                label="pay_admission_fees"
                onAction={onCollectFees}
              />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentAdmissionPayFees;
