import { useState } from "react";
import style from "../StudentAdmissionFees.module.css";
import dayjs from "dayjs";
import { useTranslation } from "react-i18next";
import StudentAdmissionFeesCardReceipt from "../../TransactionReceipt/Admission/StudentAdmissionFeesCardReceipt";
import StudentAdmissionPayFees from "./StudentAdmissionPayFees";
const StudentAdmissionSelfFeesCardInstallmentCard = ({
  install,
  admission,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const onCloseStep = () => {
    setStep("");
  };
  const onCollectFees = () => {
    if (install?.isEnable) setStep(2);
  };

  return (
    <>
      <div
        className={style.student_admission_cash_card}
        style={{
          position: "relative",
        }}
      >
        <section className={style.admission_fees_amount}>
          <h6>
            {install?.installmentValue}{" "}
            {install?.status === "Paid" && (
              <span>
                {dayjs(install?.fee_receipt?.fee_transaction_date).format(
                  "DD-MMM-YY"
                )}
              </span>
            )}
          </h6>
          <h6>
            {t("rs_only")}
            {install?.remainAmount ?? 0}
          </h6>
        </section>
        <section className={style.admission_fees_grant}>
          {install?.status === "Paid" &&
          install?.receipt_status !== "Rejected" ? (
            <>
              <h6>
                {install?.fee_receipt?.fee_payment_mode === "By Cash"
                  ? "Offline"
                  : install?.fee_receipt?.fee_payment_mode
                  ? install?.fee_receipt?.fee_payment_mode
                  : install?.refund_status ?? ""}
              </h6>
              <h6>
                {install?.fee_receipt?.set_off_status === "Set Off"
                  ? "SetOff"
                  : install?.fee_receipt?.fee_payment_mode === "By Cash"
                  ? "Cash"
                  : install?.fee_receipt?.fee_payment_mode ===
                    "Exempted/Unrecovered"
                  ? "N/A"
                  : "Bank"}
              </h6>
            </>
          ) : (
            <>
              {install?.receipt_status === "Rejected" ? (
                <h6>
                  {`${t("reason")} : `}
                  {install?.reason}
                </h6>
              ) : (
                <h6>
                  {t("due_on")}
                  {install?.dueDate}
                </h6>
              )}
            </>
          )}
        </section>
        {install?.component?.gov ? (
          <>
            <section className={style.admission_fees_grant}>
              <h6
                style={{
                  margin: "0",
                }}
              >
                {t("applicable_fees")}
              </h6>
              <h6
                style={{
                  margin: "0",
                  fontSize: "13px",
                  color: "#121212",
                }}
              >
                {t("rs_only")}
                {install?.component?.app ?? 0}
              </h6>
            </section>
            <section className={style.admission_fees_grant}>
              <h6
                style={{
                  margin: "0",
                }}
              >
                {t("govt_fees")}
              </h6>
              <h6
                style={{
                  margin: "0",
                  fontSize: "13px",
                  color: "#121212",
                }}
              >
                {t("rs_only")}
                {install?.component?.gov ?? 0}
              </h6>
            </section>
          </>
        ) : null}
        {install?.receipt_status !== "Requested" &&
        install?.receipt_status !== "Rejected" &&
        install?.status === "Paid" ? (
          <StudentAdmissionFeesCardReceipt
            rid={install?.fee_receipt?._id}
            appId={admission?.appId?._id}
          />
        ) : (
          <button
            className={style.student_admission_collect_btn}
            style={
              install?.receipt_status === "Requested"
                ? {
                    width: "100%",
                    backgroundColor: "#B8B83E",
                  }
                : install?.receipt_status === "Rejected"
                ? {
                    width: "100%",
                    backgroundColor: "#B8B83E",
                  }
                : {
                    width: "100%",
                  }
            }
            onClick={onCollectFees}
          >
            {install?.receipt_status === "Requested"
              ? t("receipt_requested")
              : install?.receipt_status === "Rejected"
              ? t("receipt_rejected")
              : t("pay")}
          </button>
        )}
      </div>
      {step === 2 ? (
        <StudentAdmissionPayFees
          onClose={onCloseStep}
          install={install}
          admission={admission}
        />
      ) : null}
    </>
  );
};

export default StudentAdmissionSelfFeesCardInstallmentCard;
